* {
  box-sizing: border-box;
}

.page {
  min-height: 100vh;
}
.page .content-aria {
  width: calc(100% - 250px);
  min-height: 100vh;
  transition: width 0.3s;
  float: right;
  background-color: #f2f1f6;
}
.page .content-aria.no-sidebar {
  width: 100%;
}

.page .sidebar {
  width: 250px;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  background-color: #292c33;
  color: #fff;
  padding: 1rem;
  transition: left 0.3s;
}
.page .sidebar.no-sidebar {
  left: -250px;
}

.header {
  background-color: #fff;
  padding: 1rem;
}
.header .toggle-sidebar {
  cursor: pointer;
}